/* ==========================================================================
  c-segmented-control
  Used for links, for radio button behavior use c-segmented-control-radio
   ========================================================================== */

.c-segmented-control {
  width: 100%;
  display: flex;
}

.c-segmented-control__item--stretch {
  flex: 1 0 auto;
}

.c-segmented-control__item a {
  flex: 1 0 auto;
  display: block;
  padding: 0.5rem;
  text-align: center;
  text-decoration: none;
  color: #333;
  background: #DDD;
  border: 1px solid #CCC;
}

.c-segmented-control__item:first-child a {
  border-top-left-radius: 0.4rem;
  border-bottom-left-radius: 0.4rem;
}

.c-segmented-control__item:last-child a {
  border-top-right-radius: 0.4rem;
  border-bottom-right-radius: 0.4rem;
}

.c-segmented-control__item--active a {
  background: #FFF;
}

.c-segmented-control__item + .c-segmented-control__item a {
  border-left: none;
}